<template>
  <div>
    <vs-checkbox
      success
      v-for="(item, i) in requisito_data"
      :key="i"
      :val="item"
      v-model="requisitos"
    >
      <template #icon>
        <i class="fas fa-check-double"></i>
      </template>
      {{ item.requisito }}
    </vs-checkbox>

    <div class=" mt-5 flex">
      <vs-button @click="dialogconfirm = true" class=" focus:outline-none">
        Guardar
      </vs-button>

      <div v-if="imprimir_registro">
        <reportes :item_selected="item_selected" />
      </div>
    </div>

    <div v-if="item_selected" class=" flex">
      <DeclaracionJurada :item_selected="item_selected" />
      <ficha-postulante :item_selected="item_selected" />
    </div>

    <vs-dialog width="300px" not-center v-model="dialogconfirm">
      <template #header>
        <h4 class="not-margin">Confirmar Accin</h4>
      </template>

      <div class="con-content">
        <span>Seguro que desea guardar los requisitos?</span>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class=" flex">
            <vs-button transparent @click="save_data">
              Si
            </vs-button>
            <vs-button dark transparent @click="dialogconfirm = false">
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>

    <vs-dialog width="300px" not-center v-model="dialogconfirmValidar">
      <template #header>
        <h4 class="not-margin">Confirmar Accin</h4>
      </template>

      <div class="con-content">
        <span>Seguro que desea validar los datos?</span>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class=" flex">
            <vs-button transparent @click="validar_postulante()">
              Si
            </vs-button>
            <vs-button dark transparent @click="dialogconfirmValidar = false">
              Cancelar
            </vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>
  </div>
</template>

<script>
import axios from "@//plugins//axios";
import Reportes from "./reportes/Reportes.vue";
import DeclaracionJurada from "@//views//Reportes//DeclaracionJurada.vue";
import FichaPostulante from "../../../Reportes/FichaPostulante.vue";
export default {
  components: { Reportes, DeclaracionJurada, FichaPostulante },
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {
      requisito_data: [],
      requisitos: [],
      dialogconfirm: false,
      dialogconfirmValidar: false,
      imprimir_registro: false,
    };
  },

  async mounted() {
    await this.set_requisitos();
  },

  methods: {
    async save_data() {
      const loading = this.$vs.loading({
        color: "succcess",
      });
      this.dialogconfirm = false;

      try {
        let final = [];

        this.requisito_data.forEach((element) => {
          let obj = {
            postulante: this.item_selected.id,
            requisito: element.requisito_id,
            success: false,
          };

          let index = this.requisitos.findIndex(
            (item) => item.requisito == element.requisito
          );

          if (index > -1) obj.success = true;
          final.push(obj);
        });

        const { data } = await axios.post("save_requisito_postulante/", final);

        if (data) {
          this.$vs.notification({
            title: "Requisitos",
            text: "Los cambios se realizaron Correctamente! ",
            color: "success",
          });

          await this.set_requisitos();
          this.validarRequisitLocalmente(this.imprimir_registro);
        } else {
          this.$vs.notification({
            title: "Requisitos",
            text: "No se pudo realizar la accin! ",
            color: "warning",
          });
        }
      } catch (error) {
        //
        this.$vs.notification({
          title: "Requisitos",
          text: "Ocurrio un error!  ",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },

    validarRequisitLocalmente(validado) {
      this.$store.dispatch("postulante/validar_localmente", {
        id: this.item_selected.id,
        is_valid: validado,
      });
    },

    async set_requisitos() {
      var is_valid = false;
      try {
        const postulante = this.item_selected.id;
        const { data } = await axios.get("get_requisito_by_modalidad/", {
          params: {
            postulante: postulante,
          },
        });

        is_valid = true;

        this.requisito_data = data;

        this.requisitos = data.filter((row) => {
          if (row.success) return row;
        });

        is_valid = data.filter((item) => item.success == false).length <= 0;

        this.imprimir_registro = false;
        this.imprimir_registro = is_valid;
      } catch (error) {
        this.imprimir_registro = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
